.sanpham__footer{
	padding: 20px 0 30px;
	.col-2{
		h3{
			color: #404040;
		    font-weight: 700;
		    font-size: 15px;
		    line-height: 30px;
		    margin: 0;
		    margin-bottom: 5px;
		}
		a{
			color: #808080;
			margin-top: 5px;
			line-height: 21px;
			&:hover{
				color: #ed3239;
			}
		}
		.telephone{
			color: #ed3239;
			font-weight: 700;
		}
	}
}
.banquyen{
	text-align: center;
	background: #f0f0f0;
	border-top: 1px solid #e0e0e0;
	p{
		line-height: 50px;
		margin: 0;
		color: #404040;
	}
}